import React from "react"
import PropTypes from "prop-types"
import {Link, graphql} from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import TAG from "../components/tag"
import POST from "../components/post"
import PAGER from "../components/pager"

import Image from "gatsby-image";

import kebabCase from "lodash/kebabCase"

const Tags = ({data, location, pageContext}) => {
    const siteTitle = pageContext || `Title`
    const posts = data.allMarkdownRemark.nodes
    const tags = data.allMarkdownRemark

    const {tag} = pageContext
    const {edges, totalCount} = data.allMarkdownRemark
    const tagHeader = `${tag}`

    return (<Layout location={location} title={tagHeader}>
        <div class="container">
            <SEO title={tag} />
            <h1 class="heading-main"><span>{tagHeader}</span></h1>
            <TAG tag={tags} />
            <POST posts={posts} />
        </div>
    </Layout>)
}

Tags.propTypes = {
    pageContext: PropTypes.shape({tag: PropTypes.string.isRequired}),
    data: PropTypes.shape({
        allMarkdownRemark: PropTypes.shape({
            totalCount: PropTypes.number.isRequired,
            edges: PropTypes.arrayOf(PropTypes.shape({
                node: PropTypes.shape({
                    frontmatter: PropTypes.shape({title: PropTypes.string.isRequired}),
                    fields: PropTypes.shape({slug: PropTypes.string.isRequired})
                })
            }).isRequired)
        })
    })
}

export default Tags

export const pageQuery = graphql `
  query tagListQuery($tag: String) {
      site {
        siteMetadata {
          title
        }
      }
    allMarkdownRemark(sort: {
        fields: [frontmatter___date], order: DESC }
        filter: { frontmatter: { tags: { in: [$tag] }, category: { in: ["blog"] } } }
        limit: 2000
    ) {
      totalCount
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "YYYY年MM月DD日")
          title
          description
          slug
          tags
          thumnail {
            childImageSharp {
              fluid(maxWidth: 338) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      group(field: frontmatter___tags) {
          tag: fieldValue
          totalCount
      }
    }
  }
`
